        </view>
        <view class="form-content">
          <view class="form-group">
            <text class="form-label">ËÆ¢ÂçïÂè∑</text>
            <text class="form-value">{{ orderDetail.orderNo || orderDetail.order_id }}</text>
          </view>
          <view class="form-group">
            <text class="form-label">Ê≥ï‰ºöÈ°πÁõÆ</text>
            <text class="form-value">{{ orderDetail.projectName || orderDetail.project_name || 'Êú™ÊåáÂÆö' }}</text>
          </view>
          <view class="form-group">
            <text class="form-label">ËÆ¢ÂçïÁä∂ÊÄÅ</text>
            <picker @change="onStatusChange" :value="statusIndex" :range="statusOptions" range-key="label">
              <view class="picker-item">
                <text class="picker-text">{{ statusOptions[statusIndex].label }}</text>
                <text class="picker-arrow">‚ñº</text>
              </view>
            </picker>
          </view>
          <view class="form-group">
