            <view class="form-group">
              <text class="form-label">ÂßìÂêç</text>
              <input 
                v-model="applicant.name" 
                class="form-input"
                placeholder="ËØ∑ËæìÂÖ•ÂßìÂêç"
              />
            </view>
            <view class="form-group">
