<script>
export default {
  data() {
    return {
      orderId: '',
      orderDetail: null,
      loading: true,
      statusIndex: 0,
      statusOptions: [
        { label: 'ÂæÖÊîØ‰ªò', value: 'ÂæÖÊîØ‰ªò' },
        { label: 'ÂæÖÁ°ÆËÆ§', value: 'ÂæÖÁ°ÆËÆ§' },
        { label: 'Â§ÑÁêÜ‰∏≠', value: 'Â§ÑÁêÜ‰∏≠' },
        { label: 'Â∑≤ÂÆåÊàê', value: 'Â∑≤ÂÆåÊàê' },
        { label: 'Â∑≤ÂèñÊ∂à', value: 'Â∑≤ÂèñÊ∂à' }
      ]
    }
  },

  onLoad(options) {
    console.log('‚úèÔ∏è ‰∏ìÂú∫Ê≥ï‰ºöÁºñËæëËÆ¢ÂçïÈ°µÈù¢Âä†ËΩΩÔºåÂèÇÊï∞:', options)
    console.log('‚úèÔ∏è È°µÈù¢Ë∑ØÂæÑ:', getCurrentPages()[getCurrentPages().length - 1].route)
    
    if (options.id) {
      this.orderId = options.id
      console.log('‚úèÔ∏è Êé•Êî∂Âà∞ÁöÑËÆ¢ÂçïID:', this.orderId)
      this.loadOrderDetail()
    } else {
      console.error('‚ùå ËÆ¢ÂçïIDÂèÇÊï∞‰∏çÂ≠òÂú®')
      uni.showToast({
        title: 'ËÆ¢ÂçïID‰∏çÂ≠òÂú®',
        icon: 'none'
      })
      setTimeout(() => {
        this.goBack()
      }, 1500)
    }
  },

  methods: {
    async loadOrderDetail() {
      try {
        this.loading = true
        console.log('Âä†ËΩΩ‰∏ìÂú∫Ê≥ï‰ºöËÆ¢ÂçïËØ¶ÊÉÖÔºåID:', this.orderId)
        
        const fahuiManagement = uniCloud.importObject('fahui-management')
        
        // ‰∏¥Êó∂‰ΩøÁî® getOrders ÊñπÊ≥ïËé∑ÂèñËÆ¢ÂçïËØ¶ÊÉÖ
        console.log('üîÑ ‰ΩøÁî®‰∏¥Êó∂ÊñπÊ°àËé∑ÂèñËÆ¢ÂçïËØ¶ÊÉÖ...')
        const ordersResult = await fahuiManagement.getOrders()
        
        if (ordersResult.success && ordersResult.data) {
          const orderDetail = ordersResult.data.find(order => 
            order._id === this.orderId || 
            order.order_id === this.orderId || 
            order.orderNo === this.orderId
          )
          
          if (orderDetail) {
            this.orderDetail = orderDetail
            // ËÆæÁΩÆÁä∂ÊÄÅÁ¥¢Âºï
            const currentStatus = this.orderDetail.status || 'ÂæÖÊîØ‰ªò'
            this.statusIndex = this.statusOptions.findIndex(option => option.value === currentStatus)
            if (this.statusIndex === -1) this.statusIndex = 0
            
            console.log('‰∏ìÂú∫Ê≥ï‰ºöËÆ¢ÂçïËØ¶ÊÉÖÂä†ËΩΩÊàêÂäü:', this.orderDetail)
          } else {
            this.orderDetail = null
            console.error('‰∏ìÂú∫Ê≥ï‰ºöËÆ¢ÂçïËØ¶ÊÉÖÂä†ËΩΩÂ§±Ë¥•: Êú™ÊâæÂà∞ËÆ¢Âçï')
            uni.showToast({
              title: 'ËÆ¢Âçï‰∏çÂ≠òÂú®',
              icon: 'none'
            })
          }
        } else {
          this.orderDetail = null
          console.error('‰∏ìÂú∫Ê≥ï‰ºöËÆ¢ÂçïËØ¶ÊÉÖÂä†ËΩΩÂ§±Ë¥•:', ordersResult.message)
          uni.showToast({
            title: ordersResult.message || 'Âä†ËΩΩÂ§±Ë¥•',
            icon: 'none'
          })
        }
      } catch (error) {
        console.error('Âä†ËΩΩ‰∏ìÂú∫Ê≥ï‰ºöËÆ¢ÂçïËØ¶ÊÉÖÂ§±Ë¥•:', error)
        this.orderDetail = null
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•',
          icon: 'none'
        })
      } finally {
        this.loading = false
      }
    },

    onStatusChange(e) {
      this.statusIndex = e.detail.value
      this.orderDetail.status = this.statusOptions[this.statusIndex].value
    },

    formatDate(date) {
      if (!date) return 'Êú™Áü•Êó∂Èó¥'
      
      const d = new Date(date)
      return d.toLocaleString('zh-CN')
    },

    async saveOrder() {
      if (!this.orderDetail) {
        uni.showToast({
          title: 'ËÆ¢ÂçïÊï∞ÊçÆ‰∏çÂ≠òÂú®',
          icon: 'none'
        })
        return
      }

      try {
        this.loading = true
        uni.showLoading({ title: '‰øùÂ≠ò‰∏≠...' })
        
        const fahuiManagement = uniCloud.importObject('fahui-management')
        
        // ‰∏¥Êó∂‰ΩøÁî® updateOrderStatus ÊñπÊ≥ïÊõ¥Êñ∞ËÆ¢Âçï
        console.log('üîÑ ‰ΩøÁî®‰∏¥Êó∂ÊñπÊ°àÊõ¥Êñ∞ËÆ¢Âçï...')
        const result = await fahuiManagement.updateOrderStatus({
          orderId: this.orderId,
          status: this.orderDetail.status,
          operator: 'ÁÆ°ÁêÜÂëòÁºñËæë'
        })
        
        if (result.success) {
          uni.hideLoading()
          uni.showToast({
            title: '‰øùÂ≠òÊàêÂäü',
            icon: 'success'
          })
          
          // ËøîÂõû‰∏ä‰∏ÄÈ°µ
          setTimeout(() => {
            this.goBack()
          }, 1500)
        } else {
          uni.hideLoading()
          uni.showToast({
            title: result.message || '‰øùÂ≠òÂ§±Ë¥•',
            icon: 'none'
          })
        }
      } catch (error) {
        uni.hideLoading()
        console.error('‰øùÂ≠òËÆ¢ÂçïÂ§±Ë¥•:', error)
        uni.showToast({
          title: '‰øùÂ≠òÂ§±Ë¥•',
          icon: 'none'
        })
      } finally {
        this.loading = false
      }
    },

    goBack() {
      uni.navigateBack()
    }
  }
}
</script>

